html[data-theme='dark'] {
  // transition colors from other theme
  --html-background-color-from-transition: #6C7989;
  --html-background-color-to-transition: #434B55;
  --section-background-from-transition: #fafafa;
  --section-background-to-transition: #fafafa;
  --header-background-from-transition: #DDFBFC;
  --header-background-to-transition: #C6EAFA;

  --html-background-color-from: #43424a;
  --html-background-color-to: #222228;

  --media-html-background: #232326;

  --body-color: #fafafa;

  --section-background-from: #313036;
  --section-background-to: #232326;
  --section-border-top: #313036;

  --wrapper-background: #313036;

  --link-color: #63ff8c;

  --header-background: #313036;
  --header-background-from: #313036;
  --header-background-to: #232326;

  --header-border-bottom: #1b1b1d;
  --header-p: #fafafa;
  --header-p-view-text-shadow: none;

  --header-h1-color: #63ff8c;
  --header-h1-textshadow: none;

  --h1: #fafafa;
  --h2: #fafafa;
  --h3: #fafafa;
  --h4: #fafafa;
  --h5: #fafafa;
  --h6: #fafafa;

  // ``` code block background
  --highlighter-background: #3A3C42;
  --pre-color: #f8f8f2;

  // inline code `code`
  --code-background-color: #3d4046;
  --code-color: #fdfdfd;

  --svg-footer-color: #ffffff;

  --blockquote-background: #3A3C42;
  --blockquote-color: #fafafa;

  --th-td-border-bottom: #313036;
  --dt-color: #fafafa;
  --th-color: #fafafa;

  --hr-background: #131314;

  --strong-color: #fafafa;

  --footer-color: #fafafa;
  --footer-a-color: #fafafa;

  --paginator-shadow: none;
  --paginator-background: #3d4046;
  --current-page: #65676f;
  --current-page-color: var(--body-color);
  --hover-page: #565962;
  --hover-a-color: #fdfdfd;
  --paginator-color: var(--body-color);
}
