html[data-theme='light'] {
  // transition colors from other theme
  --html-background-color-from-transition: #43424a;
  --html-background-color-to-transition: #222228;
  --section-background-from-transition: #313036;
  --section-background-to-transition: #232326;
  --header-background-from-transition: #313036;
  --header-background-to-transition: #232326;

  --html-background-color-from: #6C7989;
  --html-background-color-to: #434B55;

  --media-html-background: #434B55;

  --body-color: #555;

  --section-background-from: #fafafa;
  // originally to is #DEDEDE, but keeping the same color to match comment system theme
  --section-background-to: var(--section-background-from);
  --section-border-top: #fff;

  --wrapper-background: #DEDEDE;

  --link-color: #06c;

  --header-background: #C6EAFA;
  --header-background-from: #DDFBFC;
  --header-background-to: #C6EAFA;

  --header-border-bottom: #B2D2E1;
  --header-p: #61778B;
  --header-p-view-text-shadow: rgba(#fff, 0.9) 0 1px 0;

  --header-h1-color: #069;
  --header-h1-textshadow: rgba(#fff, 0.9) 0 1px 0;

  --h1: #222;
  --h2: #393939;
  --h3: #494949;
  --h4: #494949;
  --h5: #494949;
  --h6: #494949;

  // ``` code block background
  --highlighter-background: #3A3C42;
  --pre-color: #f8f8f2;

  // inline code `code`
  --code-background-color: #4f525a;
  --code-color: #fdfdfd;

  --svg-footer-color: #ffffff;

  --blockquote-background: #dcdcdc;
  --blockquote-color: #222;

  --th-td-border-bottom: #aaa;
  --dt-color: #222;
  --th-color: #222;

  --hr-background: #aaa;

  --strong-color: #222;

  --footer-color: #ccc;
  --footer-a-color: #fff;

  --paginator-shadow: rgb(99 99 99 / 50%) 0px 1px 5px 1px;
  --paginator-background: #fafafa;
  --current-page: #1b8dff;
  --current-page-color: #fafafa;
  --hover-page: #c2c2c2;
  --hover-a-color: #fafafa;
  --paginator-color: var(--body-color);
}
