@import "rouge-dark";
@import url('https://fonts.googleapis.com/css?family=Lato:400italic,700italic,400,700');
@import "themes/dark-theme";
@import "themes/light-theme";
@import "themes/theme-default";
@import "assets/devicon.min";

html {
  height: 100%;
}

body {
  padding:50px 0;
  margin:0;
  font:14px/1.5 Lato, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight:400;
  min-height: calc(100% - 100px);
}

.wrapper {
  width:740px;
  margin:0 auto;
  border-radius: 8px;
}

header, section, footer {
  display:block;
}

a {
  text-decoration:none;
}

p {
  margin:0 0 20px;
  padding:0;
}

strong {
  font-weight:700;
}

header {
  border-radius: 8px 8px 0 0;
  position:relative;
  padding:15px 20px;

  h1 {
    margin:0;
    padding:0;
    font-size:24px;
    line-height:1.2;
  }

  &.without-description h1 {
    margin:10px 0;
  }

  p {
    margin:0;
    font-size:13px;

    &.view {
      //display:none;
      font-weight:700;
      -webkit-font-smoothing:antialiased;

      small {
        font-weight:400;
      }
    }
  }

  ul {
    margin:0;
    padding:0;
    list-style:none;
    position:absolute;
    z-index:1;
    right:20px;
    top:20px;
    height:38px;
    padding:1px 0;
    border-radius:5px;
    width:auto;

    &:before {
      content:'';
      position:absolute;
      z-index:-1;
      left:-5px;
      top:-4px;
      right:-5px;
      bottom:-6px;
      border-radius: 8px;
    }

    li {
      width:79px;
      float:left;
      height:38px;

      &.single {
        border:none;
      }
    }

    li + li {
      width:78px;
    }

    li + li + li {
      border-right:none;
      width:79px;
    }

    a {
      line-height:1;
      font-size:11px;
      display:block;
      text-align:center;
      font-weight:400;
      padding-top:6px;
      height:40px;

      strong {
        font-size:14px;
        display:block;
        -webkit-font-smoothing:antialiased;
      }
    }
  }
}

section {
  padding:15px 20px;
  font-size:15px;
  border-radius: 0 0 8px 8px;
  position:relative;
}

h1, h2, h3, h4, h5, h6 {
  padding:0;
  margin:0 0 20px;
  line-height:1.2;
}

p, ul, ol, table, pre, dl {
  margin:0 0 20px;
}

h1, h2, h3 {
  line-height:1.1;
}

h1 {
  font-size:28px;
}

blockquote {
  margin:0 -20px 20px;
  padding:15px 20px 1px 40px;
  font-style:italic;
}

img {
  max-width:100%;
}

code, pre, .bubble {
  //font-family:Monaco, Bitstream Vera Sans Mono, Lucida Console, Terminal;
  font-size:12.5px;
  overflow-x:auto;
}

pre {
  padding:20px;
  margin:0 -20px 20px;

  li & {
    margin-left:-60px;
    padding-left:60px;
  }
}

table {
  width:100%;
  border-collapse:collapse;
}

th, td {
  text-align:left;
  padding:5px 10px;
}

dt {
  font-weight:700;
}

small {
  font-size:11px;
}

hr {
  border:0;
  height:1px;
  margin:0 0 20px;
}

footer {
  width:640px;
  margin:0 auto;
  padding:20px 0 0;
  overflow:hidden;

  a {
    font-weight:bold;
  }

  p {
    float:left;
  }

  p + p {
    float:right;
  }
}

@media print, screen and (max-width: 740px) {
  body {
    padding:0;
  }

  .wrapper {
    border-radius: 0;
    box-shadow: none;
    width:100%;
  }

  footer {
    border-radius: 0;
    padding:20px;
    width:auto;

    p {
      float:none;
      margin:0;
    }

    p + p {
      float:none;
    }
  }
}

@media print, screen and (max-width:580px) {
  header ul {
    display:none;
  }

  header p.view {
    display:block;
  }
}
